import React from 'react';
import { withStyles } from '@material-ui/core';

const styles = theme => ({
	anchor: {
		display: 'block',
		position: 'relative',
		top: theme.spacing(-10),
		visibility: 'hidden'
	}
});

function AnchorTarget({ anchorId, classes }) {
	return (
		<a
			id={anchorId}
			className={classes.anchor}
		/>
	);
}

export default withStyles(styles)(AnchorTarget);
